@import url("https://fonts.googleapis.com/css2?family=Raleway:ital@0;1&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,700;1,700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,900;1,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Cardo:ital,wght@0,400;0,700;1,400&display=swap");
.features {
  padding: 116px 0;
}
.features .container {
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
}
.features .wrapper {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 87px 30px;
}
.features .card {
  width: 370px;
  text-align: center;
}
.features .card__title {
  color: rgb(45, 48, 51);
  font-family: Raleway;
  font-size: 24px;
  font-weight: 900;
  line-height: 28px;
  text-align: center;
  text-transform: uppercase;
  width: 100%;
  margin: 29px 0 20px;
  padding: 27px 0px;
}
.features .card__text {
  color: rgb(108, 114, 121);
  font-family: Raleway;
  font-size: 18px;
  font-weight: 400;
  line-height: 21px;
  text-align: center;
  width: 100%;
}